.landing {
  position: relative;
  /* background: url("../img/showcase.jpg") no-repeat; */
  background-size: cover;
  background-position: center;
  height: 100vh;
  margin-top: -24px;
  margin-bottom: -50px;
}
#landing-header {
  z-index: 1;
  position: relative;
  text-align: center;
  padding-top: 20vh;
  color: rgb(255, 254, 254);
}

.slideshow {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 55px;
  left: 0;
  z-index: 0;
  position: absolute;
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  list-style: none;

  margin: 0;
  padding: 0;
  background-image: url(https://images.unsplash.com/photo-1591277187956-f1322a8330b5?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1351&q=80);
}

.landing-inner {
  padding-top: 80px;
}

.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.card-form {
